import { useCallback, useState } from 'react';

export type MonthAndYear = { month: number; year: number };

export const useMonthAndYear = (now: Date): [MonthAndYear, () => void, () => void] => {
    const [monthAndYear, setMonthAndYear] = useState<MonthAndYear>(() => ({
        month: now.getMonth(),
        year: now.getFullYear(),
    }));

    const prevMonth = useCallback(
        () =>
            setMonthAndYear(({ month, year }) => ({
                month: month === 0 ? 11 : month - 1,
                year: month === 0 ? year - 1 : year,
            })),
        []
    );
    const nextMonth = useCallback(
        () =>
            setMonthAndYear(({ month, year }) => ({
                month: month === 11 ? 0 : month + 1,
                year: month === 11 ? year + 1 : year,
            })),
        []
    );

    return [monthAndYear, prevMonth, nextMonth];
};
