import { NavigationState, Store } from '../store';
import { Persistore } from 'persistore';

const NAV_KEY = 'navigation';

const _set = (navigation: NavigationState): void => {
    Persistore.set(NAV_KEY, navigation);
    Store.set({ navigation });
};

const navigateTo = (navigation: NavigationState) => () => _set(navigation);

const _get = (): NavigationState => {
    const current = Persistore.get(NAV_KEY);
    return Object.values(NavigationState).find((n) => n === current) || NavigationState.CALENDAR;
};

Store.set({ navigation: _get() });

export const NavigationStore = { navigateTo };
