import React, { useEffect } from 'react';
import { useSub } from '../store';
import { ReviewRow } from './ReviewRow';
import { loadReviews } from '../util/load-reviews';

export const ReviewPage = () => {
    const { version, reviews, since } = useSub(({ reviewsData }) => reviewsData);

    useEffect(() => {
        if (!reviews.length) {
            loadReviews();
        }
    }, [reviews.length]);

    return (
        <div className="reviews">
            <h1>Kronenliste</h1>
            <table className="pure-table">
                <thead>
                    <tr>
                        <th>Name</th>
                        <th>
                            <span role="img" aria-label="Kronen">
                                👑
                            </span>
                        </th>
                        <th>
                            <span role="img" aria-label="Gelbe Karten">
                                😩
                            </span>
                        </th>
                        <th>
                            <span role="img" aria-label="Rote Karte">
                                😡
                            </span>
                        </th>
                        <th>
                            <span role="img" aria-label="Fehlende Kronen">
                                🚫
                            </span>
                        </th>
                    </tr>
                </thead>
                <tbody>
                    {reviews.map((review) => (
                        <ReviewRow key={review.name} review={review} />
                    ))}
                </tbody>
            </table>
            Die Liste wird geführt seit: {since}
            <br />
            Aktualisiert am: {version}
            <ul>
                <li>
                    <span role="img" aria-label="Kronen">
                        👑
                    </span>{' '}
                    - Anzahl der Kronen
                </li>
                <li>
                    <span role="img" aria-label="Gelbe Karten">
                        😩
                    </span>{' '}
                    - Anzahl der gelben Karten
                </li>
                <li>
                    <span role="img" aria-label="Rote Karte">
                        😡
                    </span>{' '}
                    - Anzahl der roten Karten
                </li>
                <li>
                    <span role="img" aria-label="Fehlende Kronen">
                        🚫
                    </span>{' '}
                    - Anzahl der noch fehlenden Kronen
                </li>
            </ul>
        </div>
    );
};
