import React, { useEffect } from 'react';
import { Util } from '../util/util';
import { useSub } from '../store';
import { loadDetentions } from '../util/load-detentions';

enum ProgressColor {
    GOOD = 'green',
    WARN = 'orange',
    BAD = 'red',
}

const calculateProgressColor = (min: number): ProgressColor =>
    min < 15 ? ProgressColor.GOOD : min < 30 ? ProgressColor.WARN : ProgressColor.BAD;

const calculateWidthPercent = (min: number): number => Math.min(100, min / 0.45);
const calculateWidth = (min: number): string => calculateWidthPercent(min) + '%';

export const DetentionsPage: React.FC = () => {
    const { version, detentions, since } = useSub(({ detentionsData }) => detentionsData);

    useEffect(() => {
        if (!detentions.length) {
            loadDetentions();
        }
    }, [detentions.length]);

    return (
        <div className="detentions">
            <h1>Verspätungen</h1>
            {detentions.map(({ name, min, detentions, finished }) => (
                <div className="detention" key={name}>
                    <div>{name}</div>
                    <div className="detention__progress">
                        <div
                            className="detention__progress--value"
                            style={{ width: calculateWidth(min), backgroundColor: calculateProgressColor(min) }}
                        />
                        <div className="detention__progress--desc">{min} min</div>
                    </div>
                    {detentions > 0 && (
                        <div className={Util.classNames('detention__statistic', detentions > finished && 'bad')}>
                            {finished} / {detentions}
                        </div>
                    )}
                </div>
            ))}
            <p>
                Die Liste wird geführt seit: {since}
                <br />
                Aktualisiert am: {version}
            </p>
            <h3>Erklärung:</h3>
            <p>
                Sobald der Balken mit 45 min Verpätungszeit aufgefüllt ist, ist ein Nacharbeiten der Stunde
                erforderlich. Die Zahlen am Ende der Reihe z.B. 1 / 2 bedeuten, dass 2 mal das Nacharbeiten der Stunde
                erforderlich war und bereits 1 mal nachgeholt wurde.
            </p>
        </div>
    );
};
