import React from 'react';
import { DisplayDate } from './useDisplayDates';
import { Util } from '../../util/util';
import { DisplayEventView } from './DisplayEventView';

export type DisplayDateViewProps = { date: DisplayDate; month: number; now: Date };

export const DisplayDateView: React.FC<DisplayDateViewProps> = ({ date: { date, events }, month, now }) => (
    <div
        className={Util.classNames(
            'date',
            date.getMonth() !== month && 'inactive',
            +date === +now && 'current',
            date.getDay() === 1 && 'monday'
        )}>
        <span className="value">{date.getDate()}</span>
        {events.map((event, index: number) => (
            <DisplayEventView event={event} key={index} />
        ))}
    </div>
);
