import React from 'react';
import { IconChevronLeft, IconChevronRight } from '../icons/icon';
import { CalendarEvent } from '../../store';
import { useMonthAndYear } from './useMonthAndYear';
import { useDisplayDates } from './useDisplayDates';
import { DisplayDateView } from './DisplayDateView';

const MONTH_NAMES = [
    'Januar',
    'Februar',
    'März',
    'April',
    'Mai',
    'Juni',
    'Juli',
    'August',
    'September',
    'Oktober',
    'November',
    'Dezember',
];
const WEEKDAY_NAMES = ['Mo', 'Di', 'Mi', 'Do', 'Fr', 'Sa', 'So'];

const _NOW = new Date();
const NOW = new Date(_NOW.getFullYear(), _NOW.getMonth(), _NOW.getDate());

export const Calendar: React.FC<{ events: CalendarEvent[] }> = ({ events }) => {
    const [{ month, year }, prevMonth, nextMonth] = useMonthAndYear(NOW);
    const dates = useDisplayDates(year, month, events);

    return (
        <div className="calendar">
            <div className="month">
                <span onClick={prevMonth} className="arrow">
                    <IconChevronLeft />
                </span>
                <span className="value">
                    {MONTH_NAMES[month]} {year}
                </span>
                <span onClick={nextMonth} className="arrow">
                    <IconChevronRight />
                </span>
            </div>
            <div className="grid">
                {WEEKDAY_NAMES.map((weekday) => (
                    <div key={weekday} className="weekday">
                        {weekday}
                    </div>
                ))}
                {dates.map((date) => (
                    <DisplayDateView date={date} month={month} now={NOW} key={+date.date} />
                ))}
            </div>
        </div>
    );
};
