import { createStore } from 'react-use-sub';

export type CalendarEvent = {
    title: string;
    start: Date;
    end: Date;
    desc?: string;
    color?: string;
};

export type Review = {
    name: string;
    crowns: number;
    warnings: number;
    evils: number;
    necessary?: number;
    rewards?: number;
};
export type ReviewsData = {
    reviews: ReadonlyArray<Review>;
    version: string;
    since: string;
};

export type Detention = {
    name: string;
    min: number;
    detentions: number;
    finished: number;
};
export type DetentionsData = {
    detentions: ReadonlyArray<Detention>;
    version: string;
    since: string;
};

export enum NavigationState {
    CALENDAR = 'calendar',
    DUTIES = 'duties',
    REVIEW = 'review',
    DETENTIONS = 'detentions',
}

type State = {
    navigation: NavigationState;
    events: CalendarEvent[];
    reviewsData: ReviewsData;
    detentionsData: DetentionsData;
    selectedEvent: CalendarEvent | null;
    accessGranted: boolean;
};

const [useSub, Store] = createStore<State>({
    navigation: NavigationState.CALENDAR,
    events: [],
    reviewsData: { version: '', reviews: [], since: '' },
    detentionsData: { version: '', detentions: [], since: '' },
    selectedEvent: null,
    accessGranted: false,
});

export { useSub, Store };
