import React, { useCallback, useEffect, useState } from 'react';
import { Util } from '../util/util';
import { Persistore } from 'persistore';
import { Store, useSub } from '../store';
import { IconChevronRight } from './icons/icon';

const expectedPw = '711c8940d4e7779c789cdfebb099aebc9d8bfc8345703a735a1e1e00714dd8fa';

const digest = async (message: string) => {
    const msgUint8 = new TextEncoder().encode(message);
    const hashBuffer = await crypto.subtle.digest('SHA-256', msgUint8);
    const hashArray = Array.from(new Uint8Array(hashBuffer));
    return hashArray.map((b) => b.toString(16).padStart(2, '0')).join('');
};

const _KEY = digest(expectedPw);

const getAccess = (): Promise<boolean> => _KEY.then((key) => !!Persistore.get(key));
const grantAccess = () => Store.set({ accessGranted: true });
const saveAccess = () => _KEY.then((key) => Persistore.set(key, 'true')).then(grantAccess);

const ANIMATION_TIME = 3_000;

export const LoginPage: React.FC = () => {
    const [hideLoginPage, setHideLoginPage] = useState(false);
    const [hideLogin, setHideLogin] = useState(true);
    const [submitFailed, setSubmitFailed] = useState(0);
    const [pw, setPw] = useState('');
    const accessGranted = useSub(({ accessGranted }) => accessGranted);

    useEffect(() => {
        getAccess().then((access) => {
            if (access) grantAccess();
            else setHideLogin(false);
        });
    }, []);

    useEffect(() => {
        if (accessGranted) setTimeout(() => setHideLoginPage(true), ANIMATION_TIME);
    }, [accessGranted]);

    const onSubmit = useCallback(
        async (event) => {
            event.preventDefault();
            if ((await digest(pw)) === expectedPw) {
                await saveAccess();
                setSubmitFailed(0);
            } else {
                setSubmitFailed((count) => count + 1);
            }
        },
        [pw]
    );
    const onChange = useCallback((event) => setPw(event.target.value), []);

    if (hideLoginPage) return null;

    return (
        <div className={Util.classNames('login', accessGranted && 'login--open')}>
            <div className="login__welcome-left">
                <div>Willko</div>
                {hideLogin || <div className="small">Password</div>}
            </div>
            <div className="login__welcome-right">
                <div>mmen</div>
                {hideLogin || (
                    <div className="small">
                        <form onSubmit={onSubmit}>
                            <input
                                key={submitFailed}
                                className={Util.classNames(submitFailed && 'error')}
                                type="password"
                                onChange={onChange}
                                value={pw}
                            />
                            <button>
                                <IconChevronRight />
                            </button>
                        </form>
                    </div>
                )}
            </div>
        </div>
    );
};
