import React from 'react';
import { DisplayEvent } from './useDisplayDates';
import { Util } from '../../util/util';

export type DisplayEventViewProps = { event: DisplayEvent };

export const DisplayEventView: React.FC<DisplayEventViewProps> = ({ event }) => {
    if (!event) return null;
    const { title, zIndex, width, className, row, select, backgroundColor } = event;
    return (
        <div
            onClick={select}
            style={{ width, zIndex, backgroundColor }}
            className={Util.classNames('event', 'event-' + row, className)}>
            {title || <>&nbsp;</>}
        </div>
    );
};
