import React, { useEffect } from 'react';
import { useSub } from '../store';
import { SelectedEvent } from './SelectedEvent';
import { Calendar } from './calendar/Calendar';
import { loadCalendarEvents } from '../util/load-calendar-events';

export const CalendarPage = () => {
    const { selectedEvent, events } = useSub(({ selectedEvent, events }) => ({ selectedEvent, events }));

    useEffect(() => {
        if (!events.length) {
            loadCalendarEvents();
        }
    }, [events.length]);

    return (
        <div className="calendar-page">
            <h1>Termine</h1>
            <Calendar events={events} />
            {selectedEvent && <SelectedEvent selectedEvent={selectedEvent} />}
        </div>
    );
};
